.order {
  padding: 40px;
  margin: 20px 0;
  border: 1px solid lightgray;
  background-color: white;
  position: relative;
}
.order_id{
  position: absolute;
  top: 40px;
  right: 20px;
}
.order_total{
   font-weight: 500;
   text-align: right;
}
@media screen and (max-width:800px) {
  .left, .main, .right {
    width:100%; /* The width is 100%, when the viewport is 800px or smaller */
  }
}